import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {ConfigProvider} from "../../providers/config/config";



@IonicPage()
@Component({
  selector: 'page-cash-by-code-beneficiary-selection',
  templateUrl: 'cash-by-code-beneficiary-selection.html',
})
export class CashByCodeBeneficiarySelectionPage  {

  accountNo;
  transferDetails01;
  beneficiaryList;
  searchTerm;
  amount;
  filterData = [];

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public config: ConfigProvider) {

    this.accountNo = this.navParams.get("accountNo");
    this.transferDetails01 = JSON.parse(this.navParams.get("transferDetails01"));
    let beneficiaries=this.navParams.get("beneficiaryList");
    this.beneficiaryList=JSON.parse(beneficiaries);
    this.amount = this.navParams.get("amount");

    if (this.beneficiaryList == null) {
      this.beneficiaryList = [];
    }
    this.filterData = this.beneficiaryList;
  }

  setFilteredItems = function () {
    this.filterData = this.beneficiaryList.filter((dest) => {
      return ((dest.subscriptionValue.toLowerCase().indexOf(this.searchTerm.toLowerCase()) > -1) ||
        (dest.subscriptionDesc.toLowerCase().indexOf(this.searchTerm.toLowerCase()) > -1));
    });
  };

  selectDestAccount = function (selectedAccount) {
    this.navCtrl.pop({animate: false});
    this.navCtrl.push('CashByCode02Page', {
      'accountNo': this.accountNo,
      'transferDetails01': JSON.stringify(this.transferDetails01),
      'selectedAccount': JSON.stringify(selectedAccount),
      'sourcePage':'listPage',
      'amount' : this.amount
    });
  };

  ionViewDidLoad() {
  }

}



